<template>
  <div>
    <v-container>
      <custom-snackbar ref="customSnackbar"></custom-snackbar>
      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Audit Assignments</v-card-title>
            <v-card-text>
              <v-list>
                <v-list-tile-title>
                  <v-layout>
                    <v-flex md1>Status</v-flex>
                    <v-flex md5>Provider</v-flex>
                    <v-flex md1>Audit Date</v-flex>
                    <v-flex md1>Med System</v-flex>
                    <v-flex md1>Billing System</v-flex>
                    <v-flex md1>Audit Type</v-flex>
                    <v-flex md1>In Progress</v-flex>
                    <v-flex md1>Completed</v-flex>
                  </v-layout>
                </v-list-tile-title>
                <template v-for="(item, index) in audits">
                  <v-list-tile
                    :key="index"
                    @click="auditPhysician(item.npi, item.provider_audit_id)"
                  >
                    <v-layout>
                      <v-flex md1>{{ getStatus(item) }}</v-flex>
                      <v-flex md5>
                        <v-layout row wrap>
                          <v-flex md12>{{ item.provider_name }}</v-flex>
                        </v-layout>
                        <v-layout row wrap>
                          <v-flex md12>{{ item.entity_name }} - {{ item.group_practice }}</v-flex>
                        </v-layout>
                      </v-flex>
                      <v-flex md1>{{ item.audit_date }}</v-flex>
                      <v-flex md1>{{ item.med_system }}</v-flex>
                      <v-flex md1>{{ item.billing_system }}</v-flex>
                      <v-flex md1>{{ item.audit_type }}</v-flex>
                      <v-flex md1>{{ calcIncomplete(item) }}</v-flex>
                      <v-flex md1>{{ item.complete }}</v-flex>
                    </v-layout>
                  </v-list-tile>
                </template>
              </v-list>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    <confirmation-dialog
      ref="confirmationDialog"
      title="Confirm"
      message="Are you sure you want to cancel your changes?"
    ></confirmation-dialog>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import ConfirmationDialog from "@/components/ConfirmationDialog";
import CustomSnackbar from "@/components/Snackbar";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: []
  }),
  async mounted() {
    httpService.get(
      `${Config.apiRoot}/audit/dashboard/${this.currentUser.id}`,
      (status, data) => {
        this.audits = data.message;
      }
    );
  },
  computed: {
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    }
  },
  methods: {
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.complete > 0 && this.calcIncomplete(provider) === 0) {
        return "Completed";
      }
      return "In Progress";
    },
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id }
      });
    },
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    }
  }
};
</script>